export default {
	data() {
		return {
			id:null,
			userId: null,
			name: '',
			phone: '',
			postion: '',
			isDefault: false
		}
	},
	onLoad(options) {
		 var detail = JSON.parse(options.detail);
		 if(detail.isDefault == 1){
		   var isDefault = true;
		 }else{
		   var isDefault = false;
		 }
		this.id=detail.id,
		this.name=detail.name,
		this.phone=detail.telephone,
		this.postion=detail.address,
		this.isDefault=isDefault
	},
	methods: {
		save: function() {
			var userId = this.userId;
			var name = this.name;
			var phone = this.phone;
			var postion = this.postion;
			var isDefault = this.isDefault;
			if (name == '' || name == null) {
				uni.showToast({
					title: '请输入姓名',
					icon: 'none',
					duration: 1000
				})
			} else if (phone == '' || phone == null) {
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none',
					duration: 1000
				})
			} else if (phone.length != 11) {
				uni.showToast({
					title: '请输入11位的手机号码',
					icon: 'none',
					duration: 1000
				})
			} else if (postion == '' || postion == null) {
				uni.showToast({
					title: '请输入详细地址',
					icon: 'none',
					duration: 1000
				})
			} else {
				//上传添加地址
				var that = this;
				if (isDefault) {
					isDefault = 1;
				} else {
					isDefault = 0;
				}
				var data = {
					'userId': userId,
					'name': name,
					'telephone': phone,
					'address': postion,
					'isDefault': isDefault
				}
				console.log(data)
			}
		},
		
		input_name: function(e) {
			this.name = e.detail.value
		},
		input_phone: function(e) {
			this.phone = e.detail.value
		},
		input_postion: function(e) {
			console.log(e)
			this.postion = e.detail.value
		},
		changeIsDefault: function(e) {
			var isDefault = this.isDefault;
			this.isDefault = !isDefault;
		},
	}
}
